.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App {
  margin: 50px 100px;

  /* width: 90vw;
  height: 90vh; */
  /* padding: 95px 165px; */
}

.question__title {
  font-weight: bold;
  margin-bottom: 30px;
  text-align: left;
  margin-top: 50px;
  font-size: 22px;
}

.main_content {
  width: 50%;
  padding-right: 200px;
  font-size: 22px;
  text-align: left;
}
.content {
  opacity: 1;
  animation: 1s animation forwards;
}

@keyframes animation {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.MuiSvgIcon-root {
  opacity: 0;
  animation: 1s animation forwards;
}

.chevron__button {
  border-style: none;
  background-color: #eaeaea;
}

button:disabled {
  opacity: 0.5;
}
